@import url(https://fonts.googleapis.com/css2?family=Lato&family=Ubuntu&display=swap);
:root {
  --blue: #4369b2;
  --darker-blue: #3760ae;
  --lighter-blue:#5a8ae3;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  background: #4369b2;
  background: var(--blue);
  color: aliceblue;
}

.links {
  text-decoration: none;
  color: inherit;
}

.subtitle {
  background-color: #3760ae;
  background-color: var(--darker-blue);
  padding: 0 2.5rem;
  font-size: .9rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Ubuntu', sans-serif;
}

.group {
  width: 50%;
}

.group:nth-child(4n+1), .group:nth-child(4n+2) {
  background-color: var(--darker-blue);
}

.group:first-child {
  width: 100%;
  background-color: var(--lighter-blue);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.min-h-5 {
  min-height: 10rem;
}
.nav-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: aliceblue;
}
.icon {
  max-width: 10rem;
  max-height: 10rem;
}

.list-item {
  padding: .5rem;
  height: 8rem;
  align-items: center;
}

.list-item:nth-child(2n) {
  background-color: var(--darker-blue)
}
